@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  background-image: linear-gradient(25deg,
    rgba(83, 95, 45, 0.4),
    rgba(120, 131, 83, 0.4)),
    url(/mindful/static/media/background.795f9b70.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  font-size: 10px;
}

.App {
  width: 100vw;
  height: 100vh;
  margin: 0 auto;
  font-family: 'Raleway', sans-serif;
}

.logout-btn-container {}

#logout-btn {
  font-size: 1.5rem;
  border: none;
  background-color: rgba(233, 233, 233, 0.6);
  position: absolute;
  right: 1.67rem;
  top: 1.67rem;
}

#logout-btn:hover {
  background-color: rgba(233, 233, 233, 0.95);
}

.btns-container {
  margin: 0 auto;
  text-align: center;
  display: flex;
  flex: 1 1;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.dest-btns {
  font-size: 1.5rem;
  width: 12rem;
  height: auto;
  border: none;
  border-radius: 3px;
  box-shadow: 0 0 3px black;
  margin: 2rem 4rem;
  font-family: 'Montserrat', sans-serif;
  padding: 0.67rem;
  text-align: center;
}

.dashboard-route-btn {
  background-color: rgb(119, 84, 74);
  color: rgba(255, 255, 255, 1);
}

.dashboard-route-btn:hover {
  background-color: rgb(150, 120, 111);
}

.entry-route-btn {
  background-color: rgb(111,185,143);
  color: rgb(248,244,232);
}

.entry-route-btn:hover {
  background-color: rgb(87, 167, 122);
}

.settings-btn-container {
  position: absolute;
  left: 2rem;
  top: 2rem;
}

#settings-btn {
  font-size: 1.75rem;
  background-color: rgba(255, 255, 255, 0.308);
  border: none;
  border-radius: 5px;
}

#settings-btn:hover {
  background-color:rgba(255, 255, 255, 0.568);
}

#settings-btn:active {
  background-color: rgba(255, 255, 255, 0.459);
}

/* WELCOME */

.welcome-container {
  margin: 5rem auto 0 auto;
  display: flex;
  flex: 1 1;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  color: rgb(248,244,232);
  font-size: 5rem;
  width: 102rem;
  height: auto;
}

.welcome-login-container {
  text-align: center;
}

.welcome-text {
  display: block;
  margin: -2rem auto;
}

.welcome-text-info {
  font-size: 2rem;
  margin: 0 auto;
}

.login-btn-container {
  margin: 0 auto;
  text-align: center;
}

/* CONTAINS LOGIN FORM */
.create-container {
  margin: 3rem auto 0 auto;
  display: flex;
  flex: 1 1;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  border: none;
  box-shadow: 0 0 3px black;
  background: linear-gradient(25deg,
    rgba(0, 0, 0, 0.7),
    rgba(49, 49, 49, 0.7));
  width: 33.33rem;
  height: auto;
  padding: 2.67rem 0.33rem;
  border-radius: 10px;
}

.login-info {
  font-size: 2rem;
  display: block;
  margin: 2.67rem auto 1.33rem auto;
}

#create-btn {
  font-size: 1.5rem;
  color: black;
  background-color: gold;
  width: 18.33rem;
  height: 2.67rem;
  font-weight: 600;
  border-radius: 5px;
  border: none;
  box-shadow: 0 0 3px black;
}

#create-btn:hover {
  background-color: rgb(253, 230, 101);
}

#create-btn:active {
  background-color: gold;
}

/* LOGIN */

.login-container {
  margin-bottom: 2rem;
}

.login-form {
  font-size: 2rem;
}

.login-input-label {
  display: block;
  margin: 0.67rem auto;
}

.login-input {
  display: block;
  margin: 0 auto 2.67rem auto;
  width: 18.33rem;
  font-size: 1.5rem;
  box-shadow: 0 0 6px black;
}

.login-btn {
  display: block;
  text-align: center;
  margin: 2.67rem auto 0 auto;
  background-color: rgb(255, 226, 61);
  color: black;
  border: none;
  box-shadow: 0 0 3px black;
  width: 7.33rem;
  height: 2.33rem;
  border-radius: 5px;
  font-weight: 600;
  font-size: 1.5rem;
}

.login-btn:hover {
  background-color: rgb(253, 230, 98);
}

.login-btn:active {
  background-color: gold;
}

/* CREATE USER */

.create-parent-container {
  margin: 1rem;
}

.create-header {
  color: white;
  font-size: 4rem;
  display: block;
  margin: 0 auto;
  text-align: center;
}

.create-user-container {
  margin: 3rem auto;
  display: flex;
  flex: 1 1;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  background: linear-gradient(25deg,
    rgba(0, 0, 0, 0.7),
    rgba(49, 49, 49, 0.7));
  border: none;
  box-shadow: 0 0 3px black;
  border-radius: 5px;
  width: 33.33rem;
  height: auto;
  padding: 2.67rem 1.33rem;
}

#error-msg-container {
  margin: 1rem auto;
  text-align: center;
}

#input-error {
  font-size: 2rem;
  text-align: center;
  margin: 0 auto;
}

#create-user-form {}

.create-input-label {
  display: block;
  font-size: 2rem;
  color: white;
}

.create-input {
  display: block;
  margin: 1.33rem auto 2.67rem auto;
  font-size: 1.75rem;
  box-shadow: 0 0 6px black;
}

.create-user-btn {
  background-color: rgb(255, 226, 61);
  color: black;
  border: none;
  border-radius: 5px;
  box-shadow: 0 0 3px black;
  width: 13.33rem;
  height: 2.33rem;
  font-size: 1.5rem;
  font-weight: 600;
}

/* TERMS */

.terms-container {
  margin: 6.67rem auto 0 auto;
  display: flex;
  flex: 1 1;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
  width: 108.8rem;
  font-size: 2.5rem;
}

.terms-inner-container {
  display: flex;
  flex: 1 1;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.terms-text-container {
  width: 108.8rem;
}

.terms-header {
  font-size: 2.5rem;
  display: block;
  margin: 2rem auto;
}

.terms-text {
  font-size: 2.25rem;
  margin: 2rem auto;
}

#accept-container {
  margin: 2.67rem auto;
}

#accept-btn {
  display: block;
  margin: 1.33rem auto;
  font-size: 2rem;
  background-color: gold;
  font-weight: 600;
  width: 15.33rem;
  height: 3.33rem;
  border: none;
  box-shadow: 0 0 3px black;
  border-radius: 5px;
}

#accept-btn:hover {
  background-color: rgb(252, 221, 45);
}

#accept-btn:active {
  background-color: gold;
}

#reject-container {
  margin: 2rem auto;
}

#reject-btn {
  font-size: 1.75rem;
  display: block;
  margin: 2rem auto;
  background-color: rgb(216, 118, 118);
  border: none;
}

/* SETTINGS */

.settings-container {
  margin: 3.33rem auto;
  display: flex;
  flex: 1 1;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 108.8rem;
  height: auto;
}

#text-container {
  width: 102.4rem;
  margin: 2rem auto;
}

.delete-text {
  color: white;
  font-size: 2rem;
  line-height: 2.75rem;
}

.delete-form {
  margin: 2rem auto;
  background: linear-gradient(25deg,
    rgba(0, 0, 0, 0.7),
    rgba(40, 40, 40, 0.7));
  border: none;
  border-radius: 5px;
  width: 26.67rem;
  height: auto;
  padding: 2.67rem 1.33rem;
}

.delete-label {
  color: white;
  font-size: 1.75rem;
  display: block;
  margin: 0.67 auto;
}

.delete-input {
  font-size: 1.5rem;
  margin: 0.67rem auto 2rem auto;
  box-shadow: 0 0 5px black;
}

#delete-user-btn {
  margin: 1.33rem auto;
  display: block;
  background-color: rgb(231, 119, 119);
  border: none;
  font-size: 1.4rem;
  font-weight: 600;
  width: 12rem;
}

