/* REC VIDEOS */

.rec-videos-container {
  margin: 3.33rem auto;
  width: 108.8rem;
  height: auto;
  display: flex;
  flex: 1 1;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.video-header-link {
  display: block;
}

.videos-header {
  color: rgba(255, 255, 255, 1);
  font-size: 2rem;
}

#video-modal {
  margin: 0 auto;
  display: block;
  width: 80%;
  height: 55rem;
  background: linear-gradient(25deg, 
    rgba(0,0,0,0.6) 50%,
    rgba(0,0,0,0.4) 100%
  );
  border-radius: 10px;
}

.video-container {
  margin: 1.33rem auto;
  display: block;
}

.video-text {
  display: block;
  font-size: 1.75rem;
  color: rgba(255, 255, 255, 1);
  margin: 3.33rem auto 0 auto;
}

.iframe {
  display: block;
