/* ENTRY PAGE */

.entry-container {
  margin: 3.33rem auto;
  display: flex;
  flex: 1;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

#entry-question-text {
  font-size: 2.5rem;
  color: white;
}

.form-container {
  width: 96rem;
  max-width: 108.8rem;
}

.entry-form {
  margin: 2.67rem auto;
  font-family: 'Raleway', sans-serif;
  max-width: 121.6rem;
}

#entry-input {
  display: block;
  text-align: left;
  padding: 0.67rem;
  margin: 0 auto;
  max-width: 66.67rem;
  max-height: 40rem;
  font-family: 'Raleway', sans-serif;
  font-size: 1.75rem;
}

.submit-btn {
  display: block;
  margin: 2.67rem auto;
  font-size: 2rem;
  background-color: rgb(52,141,223);
  border: none;
  box-shadow: 0 0 3px black;
  color: rgb(248,244,232);
  width: 12rem;
  height: auto;
  padding: 0.67rem;
  border-radius: 5px;
}

.submit-btn:hover {
  background-color: rgb(36, 114, 187);
}

/* MOBILE */

@media (max-width: 900px) {
  .entry-container {
    display: block;
    margin: 8rem auto;
    width: 90%;
  }
  
  #entry-question-text {
    font-size: 2.5rem;
    color: white;
  }
  
  .form-container {
    width: 100%;
    display: block;
    margin: 2rem auto;
  }
  
  .entry-form {
    margin: 2.67rem auto;
    font-family: 'Raleway', sans-serif;
    display: block;
    text-align: center;
    width: 100%;
  }
  
  #entry-input {
    margin: 0 auto;
    width: 90%;
    font-size: 2rem;
  }
  
  .submit-btn {
    display: block;
    margin: 2.67rem auto;
    font-size: 2rem;
    background-color: rgb(52,141,223);
    border: none;
    box-shadow: 0 0 3px black;
    color: rgb(248,244,232);
    width: 13rem;
    height: auto;
    padding: 1rem;
    border-radius: 5px;
  }
  
  .submit-btn:hover {
    background-color: rgb(36, 114, 187);
  }
}
